.main-footer {
  padding: 0 30px;
  flex: 0 0 auto;
}

.main-footer__container {
  margin: 0 auto;
  padding: 30px 0;
  max-width: 1296px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  max-height: 79px;
  box-sizing: border-box;
}

.main-footer__container--border-top {
  border-top: 1px solid $white-color-opacity;
}

.main-footer__copyright, .main-footer__author {
  margin: 0;
  color: $white-color;
  font-size: 14px;
  line-height: 19px;
  font-weight: $font-weight-regular;
  font-family: $proxima-nova-font;
}

.main-footer__copyright {
  margin-right: 55px;
}

.main-footer__author {
  color: $white-color;
  font-size: 14px;
  line-height: 19px;
  font-weight: $font-weight-bold;
  font-style: normal;
  font-family: $proxima-nova-font;
}

.main-footer__time {
  margin: 0 3px;
}
